	module.exports = {"data":{"nodePost":{"title":"Frank Turner Be More Kind Tour","body":{"value":"<p>In June I had the chance to see two dates of the Be More Kind tour with Frank Turner and the Sleeping Souls. Both shows had completely different and amazing sets of openers.</p>\r\n<p>June 23, 2018 - Aragon Ballroom (Chicago)</p>\r\n<ul>\r\n<li><a href=\"http://themenzingers.com/\">The Menzingers</a> - Happy to finally get a chance to see these guys live after listening to them since Chamberlain Waits. Had the whole place singing along, even as openers.</li>\r\n<li><a href=\"http://luceromusic.com/\">Lucero</a> - I've seen Lucero before, so I knew they put on a great show and have been on the road long enough to have zero fucks left to give. Even knowing that, I was shocked that they came out and played their entire unreleased new album front to back. The new album sounds great, but would have been nice to hear a song or two I knew.</li>\r\n<li><a href=\"http://frank-turner.com/\">Frank Turner and the Sleeping Souls</a> - a surprisingly well oiled machine live. The stuff off of his new album Be More Kind sounded great live.</li>\r\n</ul> \r\n <p>June 27, 2018 - Royale (Boston)</p>\r\n<ul>\r\n<li><a href=\"https://restorations.bandcamp.com/\">Restorations</a> - Double dipped on this tour via stubhub for a chance to see this band, and they put on a fantastic opening set. Can't recommend them enough.</li>\r\n<li><a href=\"https://jeffrosenstock.bandcamp.com/\">Jeff Rosenstock</a> - since this was a one off gig he played the set acoustic (a little over half solo, the rest with some local friends backing him) which isn't necessarily the ideal way to see one of my favorite punk acts. That said, the songs still hold up. Was also cool to see <a href=\"http://www.laurastevenson.net/\">Laura Stevenson</a> filling in with him.</li>\r\n</ul>\r\n<p>I had to bail before Frank Turner in Boston because I had to get prepped for a presentation the next day, but I'm sure it was great.</p>"},"relationships":{"image":{"localFile":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":0.7499062617172854,"src":"/static/05076f7766d14e0193ee1040b6f49ab6-416f06270c7efc368e5127f4498c385c-42807.jpg","srcSet":"/static/05076f7766d14e0193ee1040b6f49ab6-416f06270c7efc368e5127f4498c385c-960f9.jpg 313w,\n/static/05076f7766d14e0193ee1040b6f49ab6-416f06270c7efc368e5127f4498c385c-c36f2.jpg 625w,\n/static/05076f7766d14e0193ee1040b6f49ab6-416f06270c7efc368e5127f4498c385c-42807.jpg 1250w,\n/static/05076f7766d14e0193ee1040b6f49ab6-416f06270c7efc368e5127f4498c385c-dbc5e.jpg 1875w,\n/static/05076f7766d14e0193ee1040b6f49ab6-416f06270c7efc368e5127f4498c385c-97be6.jpg 2000w","sizes":"(max-width: 1250px) 100vw, 1250px"}}}}},"created":1531314435}},"pathContext":{"slug":"/note/frank-turner-be-more-kind-tour/"}}

